@charset "utf-8";
/* CSS Document */
/* reset */
@mixin clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700,800');

$color-1: #0091ea;
$color-2: #00c752;
$color-3: #303b4e;
$font-1: 'Open Sans', sans-serif;
$font-2: 'Open Sans', sans-serif;

/**/
*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: $color-1;
	text-decoration: none;
	transition-duration: 0.4s;
}
a:hover, a:focus{
	text-decoration: underline;
	color: $color-3;
}
a:focus{
 	text-decoration: none;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(0, 145, 234, 0.3); }

*::selection {
  background: rgba(0, 145, 234, 0.3); }

body{
	font: 16px $font-1;
	line-height: 1.5em;
	color: #555555;
	background: #fff; 
	padding: 0;
	&.menu-active{
		overflow: hidden;
	}
}
#wrapper{
	overflow: hidden;
	position: relative;
	padding-top: 96px;
}
.home #wrapper{
	padding: 0;
}
.container-sm, .container-xs{
	margin: 0 auto;
	padding: 0 15px;
	position: relative;
}

@media (min-width:576px){
	.container,.container-sm, .container-xs{
		max-width:540px;
	}
}
@media (min-width:768px){
	.container {max-width:720px}
	.container-sm{
		max-width: 720px; 
	}
	.container-xs{
		max-width: 670px; 
	}
}
@media (min-width:992px){
	.container{max-width:960px}
	.container-sm{
		max-width: 774px;
	}
}
@media (min-width: 1200px){
	.container{max-width: 1200px}
}
@media (min-width: 1446px){
	.container{max-width: 1446px}
}



.alignright {float:right; margin: 5px 0 30px 20px;}
.alignleft {float:left; margin: 5px 30px 20px 0;}
.alignnone{
	margin: 5px 0px 20px 0;
}
.aligncenter {
	display: block;
	margin: 25px auto;
	text-align: center;
}

h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
    margin-bottom: 0.8em;
    line-height: 1.6em;
}

h1, .h1{
	font-size: 52px;
	text-transform: uppercase;
	margin-bottom: 0.7em;
	font-weight: 800;
	color: #333333;
	line-height: 1.3em;
}

h2, .h2{
	color: $color-3;
	font-weight: bold;
	font-size: 28px;
	text-transform: uppercase;
}

h3, .h3{
	font-size: 24px;
	margin-bottom: 1em;
	font-weight: 800;
	color: $color-3;
	text-transform: uppercase;
}
h4, .h4{
	font-size: 24px;
	font-weight: 400;
	text-transform: uppercase;
	margin-bottom: 0.5em;
	color: #222;
}
h5, .h5{
	font-size: 16px;
	font-weight: 600;
	text-transform: uppercase;
	margin-bottom: 0.5em;
	color: #222;
}
h6, .h6{
	font-size: 16px;
	font-weight: bold;
	margin-bottom: 0.5em;
}
.head-page{
	border-bottom: 1px solid #e5e5e5;
	background: #fff;
	margin-bottom: 50px;
	padding: 35px 0 70px;
	h1{
		margin: 0;
	}
}

iframe{
	max-width: 100%;
	height: 660px;
	display: block;
	border: none;
}


.text-wrap{
	letter-spacing: 0.4px;
	iframe{
		width: 100%;
		margin-bottom: 40px;
		height: 360px;
	}
	blockquote{
		margin: 50px 0;
		padding: 35px 0 45px;
		font-style: italic;
		color: $color-3;
		border-top: 1px solid #999999;
		border-bottom: 1px solid #999999;
		position: relative;
		letter-spacing: 1px;
		line-height: 1.8em;
		&:before, &:after{
			content: '';
			position: absolute;
			width: 48px;
			height: 46px;
			background: url('../images/blockquote.png') center center no-repeat;
			left: 0;
			top: 0;
			margin: -23px 0 0 -20px;
		}
		&:after{
			left: auto;
			top: auto;
			bottom: 0;
			right: 0;
			margin: 0 -20px -23px;
		}
		footer{
			padding-top: 20px;
			font-style: normal;
			color: #8f959f;
			letter-spacing: 1px;
			p{
				margin: 0;
			}
			.name{
				color: $color-3;
				font-weight: bold;
			}
		}
		footer.person{
			display: flex;
			align-items: center;
			margin: 0px;
			.photo{
				overflow: hidden;
				width: 60px;
				height: 60px;
				border-radius: 50%;
				margin-right: 25px;
				img{
					margin: 0;
				}
			}
			p{
				margin: 0;
			}
			strong{
				color: #222;
			}
		}
	}
	hr{
		margin: 35px 0;
	}
	p+h2, p+h3, ul+h3, ol+h3, p+h4, p+h3, p+h5, p+h6, .gallery+h2, .gallery+h3, .gallery+h4{
		margin-top: 1em;
	}
	p{
	    margin: 0 0 1.3em;
	}
	ol, ul{
		margin-bottom: 1em;
	}
	ul li{
		list-style: none;
		margin-bottom: 0.4em;
		padding-left: 30px;
		position: relative;
		&:before{
			content: "";
		    position: absolute;
		    top: 11px;
		    left: 0px;
		    width: 4px;
		    height: 4px;
		    background: $color-2;
		}
	}
	ol{
		counter-reset: myCounter;
		li{
			list-style: none;
			margin-bottom: 0.8em;
			padding-left: 30px;
			position: relative;
			&:before{
				counter-increment: myCounter;
		        content: counter(myCounter) ".";
		        color: $color-2;
		        font-weight: 600;
		        float: left;
		        margin: 0px 0px 0 0;
		        position: absolute;
		        left: 0;
		        top: 0;
			}
		}
	}
	
	img{
		margin-top: 0.3em;
		margin-bottom: 1.8em;
	}

	.alignright {float:right; margin: 5px 0 30px 20px;}
	.alignleft {float:left; margin: 5px 30px 20px 0;}
	.alignnone{
		margin: 5px 0px 20px 0;
	}

	.aligncenter {
		display: block;
		margin: 25px auto;
		text-align: center;
	}

	strong.style-2{
		color: $color-2;
	}

}

.single{
	.text-wrap blockquote{
		color: #8f959f;
		margin: 80px 0;
	}
}

.full-line{
	margin: 45px -50px 45px;
	text-align: center;
	img, iframe{
		margin: 0;
	}
	iframe{
		height: 445px;
	}
}

.page-nav{
	text-transform: uppercase;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-top: 1px solid #dedede;
	padding-top: 45px;
	margin-top: 45px;
	font-size: 14px;
}

.breadcrumbs{
	font-size: 14px;
	text-transform: uppercase;
	margin-bottom: 35px;
	color: $color-3;
	font-weight: 600;
	padding: 12px 0;
	border-bottom: 1px solid #e5e5e5;
	background: #fff;
	position: relative;
	z-index: 10;
	a{
		display: inline-block;
		margin-right: 0;
		text-decoration: none;
		color: #999999;
		span{
			color: #999999;
		}
		&:hover span{
			color: $color-1;
		}
	}
	span{
		display: inline-block;
		color: $color-3;
		&:after{
			content: '>';
			font-weight: 400;
			margin: 0 5px;
			display: inline-block;
			color: #999999;
		}
		&:last-child:after, span:after{
			display: none;
		}
	}
}

.pagination, .wp-pagenavi{
	text-align: right;
	display: block;
	clear: both;
	padding: 0 40px 0 0;
	a, span{
		display: inline-block;
		vertical-align: top;
		color: #abbeca;
		font-family: $font-1;
		text-decoration: none;
		margin: 0 2px;
		height: 34px;
		line-height: 34px;
		text-align: center;
		&.prev, &.next, &.pages, &.previouspostslink, &.nextpostslink{
			min-width: 34px;
			overflow: hidden;
			text-indent: -1000px;
			position: relative;
		}
		&.previouspostslink, &.nextpostslink{
			margin: 0;
			&:after{
				content: '';
				position: absolute;
				left: 50%;
				top: 50%;
				width: 10px;
				height: 10px;
				border-top: 2px solid #abbeca;
				border-right: 2px solid #abbeca;
				border-radius: 3px;
				transform: rotate(45deg);
				margin: -5px 0 0 -5px; 
			}
			&:hover{
				&:after{
					border-top-color: $color-2;
					border-right-color: $color-2;
				}
			}
		}
		&.previouspostslink{
			&:after{
				transform: rotate(-135deg);
			}
		}
		&.pages{
			display: none;
		}
	}
	span.current{
		color: $color-2;
	}
	a:hover{
		color: $color-2;
	}
}

/* table */
table{
	border-collapse: collapse;
	width: 100%;
	margin: 10px 0 30px;
	}
	
table th{
	padding: 5px 15px;
	text-align: center;
	font-family: $font-1;
	color: #000;
	font-size: 14px;
	line-height: 1.3em;
	border-bottom: 2px solid $color-1;
}
	
table td{
	padding: 5px 15px;
	border-bottom: 1px solid $color-1;
}

/**/
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
	background: #f9f9fa;
	border: 1px solid #e6e6e6;
	margin-bottom: 25px;
	width: 100%;
	height: 48px;
	padding: 0 15px 0 10px;
	text-indent: 5px;
	font-family: $font-1;
	font-size: 16px;
	border-radius: 0px;
	display: block;
	color: #777777;
	font-style: italic;
	&:focus{
		background: #fff;
		border-color: $color-2;
		box-shadow: 0 0 0 2px #e0f8ea;
	}
}
textarea{
	padding: 10px 15px;
	height: 96px;
	resize: none;
}
.form-group{
	margin-bottom: 35px;
	input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
		margin-bottom: 5px;
	}

	&.form-file{
		margin-bottom: 25px;
	}
}
label, .label{
	font-weight: normal;
	color: #555;
	margin-bottom: 5px;
	display: block;
	text-align: left;
}

i.help{
	display: inline-block;
	vertical-align: middle;
	color: $color-3;
	border: 1px solid $color-3;
	text-align: center;
	font-size: 12px;
	font-weight: bold;
	border-radius: 50%;
	width: 15px;
	height: 15px;
	line-height: 13px;
	font-style: normal;
	opacity: 0.60;
	cursor: pointer;
}

.wpcf7-form-control-wrap{
	display: block;
}
span.wpcf7-not-valid-tip{
	position: absolute;
    left: 0;
    bottom: -20px;
    font-size: 11px;
    width: 100%;
}
.wpcf7-not-valid{
	border-bottom-color: red !important;
}
.wpcf7 .ajax-loader{
	position: absolute;
}

div.wpcf7-response-output {
	position: absolute;
    margin: 0em;
    width: 100%;
    padding: 15px;
}
	
/**/	
::-webkit-input-placeholder {color:#777777;transition-duration:0.2s;opacity: 0.9;}
::-moz-placeholder          {color:#777777;transition-duration:0.2s;opacity: 0.9;}
:-moz-placeholder           {color:#777777;transition-duration:0.2s;opacity: 0.9;}
:-ms-input-placeholder      {color:#777777;transition-duration:0.2s;opacity: 0.9;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

/* button */
.btn{
	display: inline-block;
	min-width: 180px;
	padding: 0px 30px;
	margin: 0px 0 0px;
	color: #fff;
	background: $color-2;
	border: 2px solid $color-2;
	font-size: 16px;
	line-height: 44px;
	text-transform: uppercase;
	text-decoration: none;
	font-weight: 800;
	cursor: pointer;
	text-align: center;
	border-radius: 0px;
	position: relative;
	z-index: 0;
	box-shadow: none;
	transition-duration: 0.4s;
	overflow: hidden;
	vertical-align: middle;

	&:focus{
		background: $color-1;
		color: #fff;
	}
	&:hover{
		color: #fff;
		border-color: $color-3;
		background: $color-3;
	}

	&+&{
		margin-left: 30px;
	}
	svg{
		fill: currentColor;
		width: 15px;
		display: inline-block;
		margin: 0 8px 1px 0;
		vertical-align: middle;
	}
	
	&.btn-light{
		background: #fff;
		border-color: #fff;
		color: $color-2;
		&:hover{
			background: none;
			color: #fff;
		}
	}

	&.btn-bordered{
		border: 2px solid #fff;
		background: none;
		color: #fff;
		&:hover{
			border-color: $color-2;
			background: $color-2;
		}
	}

	&.btn-lg{
		font-size: 20px;
		font-weight: 800;
		line-height: 60px;
	}

	&.btn-shadow{
		box-shadow: 0 1px 2px 1px #dedede;
		background: #fff;
		border-color: #fff;
		color: $color-2;
		&:hover{
			border-color: $color-2;
			background: $color-2;
			color: #fff;
		}
	}
}

.btn.btn-md{
	height: 36px;
	line-height: 30px;
	text-transform: none;
}


/* pop up */
.modal, .modal-open{
	
}

.modal-backdrop{
	background-color: $color-2;
}
.modal-backdrop.show {
    opacity: .70;
    filter: alpha(opacity=70);
}

.modal-open .modal.show{
	display: flex !important;
}

.modal-dialog{
	width: 700px;
	max-width: none;
	margin: auto;
}
.modal-content{
	background: #fff;
	border: none;
	box-shadow: none;
	border-radius: 0px;
	padding: 50px 50px;
	text-align: center;
	position: relative;
	font-size: 16px;
	box-shadow: 0 5px 30px 0 rgba(0,0,0,0.2);
	.btn{
		margin-top: 30px;
	}
}

.modal button.close {
	position: absolute;
	top: 20px;
	right: 20px;
	width: 35px;
	height: 35px;
	margin: 0;
	opacity: 1;
	background: none;
	border-bottom: none;
	z-index: 10;
	&:before{
		content: '';
		width: 45px;
    	height: 3px;
	    background: #dae5ec;
	    position: absolute;
	    top: 50%;
	    left: -5px;
	    margin-top: -1px;
	    transition-duration: 0.2s;
		transform-origin: center center;
    	transform: rotate(-45deg);
	}
	&:after{
		content: '';
		width: 45px;
    	height: 3px;
	    background: #dae5ec;
	    position: absolute;
	    top: 50%;
	    left: -5px;
	    margin-top: -1px;
	    transition-duration: 0.2s;
		transform-origin: center center;
    	transform: rotate(45deg);
	}
	&:hover{
		&:before, &:after{
			background: $color-2;
		}
	}
}
@media (min-width: 1200px) {
	.btn.callback{
		margin-top: 5px;
		padding: 10px 15px;
		max-width: 150px;
		white-space: normal;
		display: flex;
		text-align: left;
		font-size: 11px;
		line-height: 1.3em;
		align-items: center;
		svg{
			min-width: 35px;
			height: 40px;
			margin-right: 15px;
		}
	}
}
@media only screen and (min-width: 768px) {
	.btn-group-vertical>.btn{
		margin-bottom: 15px;
	}
}

@media only screen and (max-width: 1199px) {
	iframe{
		height: 600px;
	}
	.modal-dialog{
		width: 600px;
	}
	.modal-content{
		padding: 40px;
	}
	h1, .h1{
		font-size: 40px;
	}
	h2, .h2{
		font-size: 28px;
	}
}			
@media only screen and (max-width:991px) {
	iframe{
		height: 450px;
	}
	h1, .h1{
		font-size: 36px;
	}
	h2, .h2{
		font-size: 28px;
	}
	h3, .h3{
		font-size: 22px;
	}
}
@media only screen and (max-width: 767px) {
	h1, .h1{
		font-size: 36px;
	}
	h2, .h2{
		font-size: 28px;
	}
	h4, .h4{
		font-size: 20px;
	}
	h5, .h5{
		font-size: 18px;
	}
	h6, .h6{
		font-size: 16px;
	}
	.head-page{
		margin-bottom: 35px;
		padding: 0px 0 25px;
	}

	iframe{
		height: 350px;
	}

	.modal-dialog{
		max-width: 320px;
	}
	.modal-content{
		margin: 0 10px;
		padding: 30px;
		.btn {
			width: 100%;
		}
	}
	.modal button.close {
		position: absolute;
		top: 10px;
		right: 10px;
		width: 15px;
		height: 15px;
		overflow: hidden;
		&:after, &:before{
			left: -15px;
		}
	}

	label, .label {
	    font-size: 14px;
	    line-height: 1.3em;
	}

	.btn{
		&.btn-lg{
			font-size: 18px;
			line-height: 44px;
		}
		&+&{
			margin-left: 15px;
		}
	}

	table{
		margin: 25px 0;
		th{
			padding: 7px 10px;
			font-size: 15px;
		}
		td{
			padding: 7px 10px;
			font-size: 14px;
		}
	}
	.breadcrumbs{
		font-size: 12px;
		margin-bottom: 25px;
	}

	.full-line{
		margin: 35px 0px 35px;
		iframe{
			height: 350px;
		}
	}

}
@media only screen and (max-width: 575px) {
	body{
		font-size: 14px;
	}
	h1, .h1{
		font-size: 32px;
	}
	h2, .h2{
		font-size: 26px;
	}
	.text-wrap ul li{
		padding-left: 20px;
	}
	.text-wrap ul li:before{
		top: 9px;
	}
	iframe{
		height: 250px;
	}
	.modal-dialog{
		width: 300px;
	}

	table{
		margin: 20px 0;
		th{
			padding: 5px 5px;
			font-size: 13px;
			font-family: $font-1;
			font-weight: 600;
		}
		td{
			padding: 5px 5px;
			font-size: 13px;
		}
	}
	.full-line{
		margin: 25px 0px;
		iframe{
			height: 250px;
		}
	}
	.single .text-wrap blockquote{
		margin: 35px 0px;
	}

	.text-wrap{
		blockquote{
			&:before, &:after{
				margin: -23px 0 0 -15px;
			}
			&:after{
				margin: 0 -15px -23px;
			}
		}
	}
}
@media only screen and (max-width: 400px) {
	h1, .h1{
		font-size: 28px;
	}
	h2, .h2{
		font-size: 24px;
	}
}

/* HEADER */
#header{
	transition-duration: 0.2s;
	width: 100%;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1000;
	background-color: $color-3;
	.header-main{
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 96px;
		padding: 0 30px;
	}

	.logo{
		position: relative;
		z-index: 10;
		margin-bottom: 12px;
	}

	.header-right{
		display: flex;
		align-items: center;
	}

	.search-button{
		position: relative;
		width: 30px;
		height: 30px;
		background: none;
		border: none;
		cursor: pointer;
		margin: 0 32px 0 10px;
		&:before{
			content: '';
			width: 8px;
			height: 8px;
			border: 2px solid #fff;
			border-radius: 50%;
			position: absolute;
			left: 50%;
			top: 50%;
			margin: -4px 0 0 -4px;
			transition-duration: 0.2s;
		}
		&:after{
			content: '';
			width: 5px;
			height: 2px;
			background: #fff;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: rotate(45deg);
			margin: 2px 0 0 2px;
			transition-duration: 0.2s;
		}
		&:hover{
			&:before{
				border-color: $color-2;
			}
			&:after{
				background: $color-2;
			}
		}
	}

	.contact-us{
		font-size: 14px;
		text-transform: uppercase;
		font-weight: 800;
		color: #fff;
		text-decoration: none;
		line-height: 96px;
		position: relative;
		margin-left: 32px;
		&:before{
			content: '';
			position: absolute;
			top: 50%;
			left: -32px;
			margin: -16px 0 0;
			width: 1px;
			height: 32px;
			background: #a0a0a1;
		}
		&:hover{
			color: $color-2;
		}
	}
}

.search-active #header .search-button, .search-active-page #header .search-button{
	&:before{
		width: 10px;
		height: 2px;
		background: #fff;
		border: none;
		border-radius: 0%;
		transform: rotate(-45deg);
		margin: -1px 0 0 -4px;
	}
	&:after{
		width: 10px;
		height: 2px;
		background: #fff;
		transform: rotate(45deg);
		margin: -1px 0 0 -4px;
	}
	&:hover{
		&:before{
			background: $color-2;
		}
		&:after{
			background: $color-2;
		}
	}
}
