/* NAV */
.nav-trigger{
	display: none;
	position: relative;
	width: 52px;
	height: 52px;
	cursor: pointer;
	float: left;
	margin-right: 20px;
	span{
		width: 32px;
		height: 4px;
		border-radius: 3px;
		background: #fff;
		position: absolute;
		top: 0;
		left: 0;
		transition-duration: 0.2s;
	}
	span{
		left: 10px;
		top: 11px;
		&:nth-child(2){
			top: 23px;
		}
		&:nth-child(3){
			top: 35px;
		}
	}
	&.active{
		span{
			transform-origin: center center;
			transform: rotate(45deg);
			top: 24px;
			&:nth-child(2){
				opacity: 0;
				transform: rotate(0);
			}
			&:nth-child(3){
				transform: rotate(-45deg);
				top: 24px;
			}
		}
	}
}

#nav{
	position: relative;
	z-index: 5;
	ul{
		margin: 0;
		position: relative;
	}
	li{
		list-style: none;
		position: relative;
		display: inline-block;
		margin: 0 4px;
		a{
			display: block;
			font-size: 14px;
			text-transform: uppercase;
			text-align: center;
			padding: 0px 20px;
			margin: 0;
			text-decoration: none;
			position: relative;
			color: #fff;
			line-height: 96px;
			&:after{
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 0%;
				height: 2px;
				background: $color-2;
				transition-duration: 0.3s;
			}
		}
		&.menu-item-has-children > .plus{
			display: none;
		}
		&:hover > a, &.current-menu-item > a{
			color: $color-2;
			&:after{
				left: 0;
				width: 100%;
			}
		}
		&.menu-item-has-children:hover > a{
			&:after{
				left: 0;
				width: 0%;
			}
		}
		ul{
			position: absolute;
			top: 100%;
			left: 0px;
			margin: -2px 0 0 0;
			display: block;
			padding: 25px 20px;
			box-shadow: none;
			background: #111111;
			z-index: 1;
			transition-duration: 0.4s;
			&:after{
				content: '';
				position: absolute;
				top: 0;
				left: 50%;
				width: 0%;
				height: 2px;
				background: $color-2;
				transition-duration: 0.2s;
			}
			li{
				display: block;
				a{
					text-align: left;
					padding: 15px 0 15px 25px;
					min-width: 305px;
					color: #999999;
					line-height: normal;
					&:after{
						display: none;
					}
				}
				&:hover > a, &.current-menu-item > a{
					color: #fff;
				}
			}
		}
	}
}
