		border-radius: 0 15px 15px 0;
		letter-spacing: 1px;
		display: inline-block;
		.icon{
			border: 1px solid #fff;
			width: 48px;
			height: 48px;
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			transition-duration: 0.2s;
			position: absolute;
			left: 0;
			top: 50%;
			margin: -24px 0 0 -40px;
			background: rgba(0,0,0,0.5);
		}
		svg{
			width: 13px;
			fill: none;
			stroke: #fff;
			stroke-width: 10%;
			margin-left: 2px;
			transition-duration: 0.2s;
		}
		&:hover{
			background: rgba(0,199,82,0.26);
			color: #fff;
			.icon{
				background: $color-2;
				border-color: $color-2;
			}
