			padding-left: 15px;
			font-size: 16px;
			opacity: 0.54;
		}
		li:nth-child(3) .text{
			opacity: 1;
		}
	}

	.next-block{
		position: absolute;
		bottom: 15px;
		left: 50%;
		width: 10px;
		height: 10px;
		border-right: 2px solid #fff;
		border-bottom: 2px solid #fff;
		transform: rotate(45deg);
		transition-duration: 0.2s;
		margin-left: -5px;
