			.text{
				.h1{
					animation-name: fadeInLeft;
					animation-delay: 0.3s;
					animation-duration: 1s;
					animation-fill-mode: both;
					strong{
						
					}
				}
				.btn{
					animation-name: fadeInRight;
					animation-delay: 0.3s;
					animation-duration: 1s;
					animation-fill-mode: both;
				}
			}
		}
	}
}
.slick-track{
	display: flex;
	.slick-slide{
		height: auto;
	}
}
.slick-arrow{
	display: block;
	width: 68px;
	height: 68px;
	border: 2px solid #dedede;
	background: none;
	transition-duration: 0.2s;
	cursor: pointer;
	position: relative;
	overflow: hidden;
	text-indent: -1000px;
	position: absolute;
	top: 50%;
	right: 45px;
	margin-top: -34px;
	z-index: 5;
	border-radius: 50%;
	&:before{
		content: '';
		width: 15px;
		height: 15px;
		border-right: 2px solid #dedede;
		border-bottom: 2px solid #dedede;
		position: absolute;
		left: 50%;
		top: 50%;	
		transition-duration: 0.2s;
		transform-origin: center center;
		transform: rotate(-45deg);
		margin: -8px 0 0 -11px;
	}
	&.slick-prev{
		left: 45px;
		right: auto;
		transform: rotate(-180deg);
		
	}
	&:hover{
		background: #ffffff;
		border-color: #ffffff;
		&:before{
			border-color: $color-3;
		}
	}
}

.slick-dots{
	margin: 0;
	padding: 0;
	position: absolute;
	width: 100%;
	left: 0;
	bottom: 20px;
	list-style: none;
	text-align: center;
	li{
		display: inline-block;
		margin: 0 6px;
		button{
			width: 12px;
			height: 12px;
			border: none;
			text-indent: -1000px;
			overflow: hidden;
			background: #fff;
			opacity: 0.3;
			border-radius: 50%;
			cursor: pointer;
		}
		&.slick-active button{
			opacity: 1;
			box-shadow: 0 0 0 2px #fff;
		}
	}
}

