/**/
input[type='checkbox'], input[type='radio'] {
  vertical-align: top;
  width: 24px;
  height: 24px;
  margin: 0 3px 0 0;
}
input[type='checkbox'] + label, input[type='radio'] + label {
	cursor: pointer;
	padding-left: 40px;
	position: relative;
	margin-bottom: 0;
	display: block;
	&:before{
		content: '';
		position: absolute;
		left: 0px;
		top: 50%;
		margin-top: -12px;
		width: 24px;
  		height: 24px;
		border: 1px solid #e6e6e6;
	}
	&:after{
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		margin: -7px 0 0 5px;
		width: 15px;
		height: 10px;
		border-bottom: 4px solid $color-2;
		border-left: 4px solid $color-2;
		border-radius: 2px;
		transform: rotate(-45deg);
		opacity: 0;
	}
}
input[type='radio'] + label {
	&:before{
		border-radius: 50%;
	}
	&:after{
		margin: -7px 0 0 5px;
		width: 14px;
		height: 14px;
		border: 4px solid $color-2;
		background: $color-2;
		border-radius: 50%;
	}
}
input[type='checkbox']:not(checked), input[type='radio']:not(checked) {
	position: absolute;
	opacity: 0;
}
input[type='checkbox']:checked + label, input[type='radio']:checked + label {
	&:before{
		border-color: $color-2;
		box-shadow: 0 0 0 2px #e0f8ea;
	}
	&:after{
		opacity: 1;
	}
}